import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/checkout.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '*',
    name: 'notfound',
    component: ()=>import('../views/notFound.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
